import React, {useState } from "react";
import Spacer from '../components/spacer';
import {useBreakpoints, useLoading} from '../hooks';

import ProductList from "../components/productList";
import Filter from "../components/filter";
import Sort from "../components/sort";
import GroupAddress from '../components/groupAddress';
import LoadPage from '../components/loadPage';
import FilterGroup from '../components/filterGroup';

import {Grid} from '@mui/material';

import {categorization } from '../mock/categorization';
import {products} from '../mock/products';

import { useScrollToTop } from "../hooks";


export function Shop(){

    const addresses = [
        {
            name:'صفحه اصلی',
            url:'/'
        },
        {
            name:'فروشگاه',
            url:'/shop'
        }
    ];

    useScrollToTop();

    const params = React.useMemo(()=>{
        const param = new URLSearchParams(window.location.search);
        return param;
    },[window.location.search]) ;
    const groupFirstTitle = params?.get('groupFirstTitle');
    const groupSecondTitle = params?.get('groupSecondTitle');
    const [search,setSearch] = useState(params?.get('search'));

    const [filters, setFilters] = useState([]);
    const [openFilter, setOpenFilter] = useState(false);
    const [sort, setSort] = useState('mostVisited');
    const {loading} = useLoading(3);
    const {matches, isDesktop, isMobile} = useBreakpoints('down','md');

    

    React.useEffect(()=>{
        const search = params?.get('search');
        if(search){
            const searchFilter = {
                name: `جستجو(${params?.get('search')?.trim()})`,
                value: `${params?.get('search')?.trim()}`
            };
            const df = filters;
            df.push(searchFilter);
            setFilters(df);
            setSearch(search);
        }
        else{
            setSearch('');
        }
            

    },[ params]);

    const firstGroupFilter = {
        name: `دسته بندی(${groupFirstTitle?.trim()})`,
        value: `${groupFirstTitle?.trim()}`
    };
    const secondGroupFilter = {
        name: `دسته بندی(${groupSecondTitle?.trim()})`,
        value: `${groupSecondTitle?.trim()}`
    };

    if(groupFirstTitle){
        const df = filters;
        df.push(firstGroupFilter);
        setFilters(df);
    }
    if(groupSecondTitle){
        const df = filters;
        df.push(secondGroupFilter);
        setFilters(df);
    }


    return(
        <div  style={{position:"relative", maxWidth:"100%"}}>
        {
            loading ? <LoadPage />
            : (
                <>
                <Spacer level={isMobile ? 8 : 2} />
                <Grid container item xs={12} flexDirection={matches ? "column-reverse" : "row"}>
                    {matches && <Spacer level={2} />}
                    <Grid container item xs={12} md={9} spacing={matches ? 2 : 0}>
                        {matches &&
                            <Grid container item xs={6} justifyContent="flex-end">
                                <Filter setOpenFilter={setOpenFilter} openFilter={openFilter} />
                            </Grid>
                        }
                        <Grid container item xs={matches ? 6 : 12} >
                            <Sort defaultSort={sort}  setSort={setSort}   />
                        </Grid>
                    </Grid>
                    {matches && <Spacer level={2} />}
                    <Grid container item xs={12} md={3} justifyContent={matches ? "flex-end" : "center"}>
                        <GroupAddress addresses={addresses} />
                    </Grid>
                </Grid>
                <Spacer level={1} />
                <Grid container item xs={12}>
                    <Grid container item xs={12} md={8} lg={9} justifyContent="center">
                        <ProductList products={products} filters={filters} sort={sort} search={search} />
                    </Grid>
                   {(openFilter || isDesktop) && 
                    <Grid container item xs={12} md={4} lg={3} justifyContent="center" 
                        style={(openFilter && !isDesktop) ? {position:"fixed",top:"0px",left:0,height:"auto",zIndex:100} 
                        : {position:"relative",height:"auto",maxHeight:"3500px",overFlow:"auto"}}>
                        <FilterGroup filters={filters} setFilters={setFilters} categorization={categorization} setOpenFilter={setOpenFilter} />
                    </Grid>
                    }
                </Grid>
                <Spacer level={2} />
                </>
            )}
        
            
        </div>
    );
}