import React,{useState} from 'react';
import {
    setLanguage,
    getLanguage,
    translate,
    t,
} from 'react-switch-lang';

import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import { useSpring, animated, to } from 'react-spring';

import EnFlag from '../../../../assets/images/en-flag.png';
import FaFlag from '../../../../assets/images/fa-flag.png';

function Lang(props){

    // const SwitchLanguage = () => {
    //     let currentLang = getLanguage();
    //     if(currentLang === 'fa'){
    //         setLanguage("en")
    //     }
    //     else{
    //         setLanguage('fa')
    //     }
    // }

    const [flip, setFlip] = useState(false);
    const fade = useSpring({
      from: { transform: `translate3d(204%, 2px, 0px)` },
      to: { transform: `translate3d(33%, 0px, 0px)` },
      reset: true,
      reverse: flip,
    //   delay: 1000,
    //   onRest: () => setFlip(!flip),
    })

    return(
        <Grid container item xs={3} sm={2} md={2} className="lang">
            <Grid container className="langBox" onClick={() => setFlip(!flip)}>
            <animated.div className="animDiv" style={fade}>
                <img src={FaFlag} alt="Persian" />
                <img src={EnFlag} alt="English" />
            </animated.div>
            </Grid>
        </Grid>
    );
}

export default translate(Lang);