import React,{useState} from 'react';
import {
    translate,
    t,
  } from 'react-switch-lang';

import Grid from '@mui/material/Grid'
import TextField from '@mui/material/TextField';
import Autocomplete from '@mui/material/Autocomplete';
import { IconButton } from '@mui/material';
import { InputAdornment } from '@mui/material';
import { BiSearch } from "react-icons/bi";

import {useBreakpoints} from '../../../../hooks';

import {useHistory} from 'react-router-dom';



function Search(props){
  const history = useHistory();
  const [showSearchBox,setShowSearchBox] = useState(false);
  const [query,setQuery] = useState('');
    const {isMobile} = useBreakpoints();
    const searchInput = [{title:'رب گوجه',price:'3434343'},{title:'سبزیجات',price:'3434343'},{title:'مرباجات',price:'3434343'},{title:'ترشیجات',price:'3434343'},]
   const handleSearch = () => {
     setShowSearchBox(!showSearchBox);
   }

   const setSearchQuery = (e) => {
    setQuery(e.target.value);
   }

   const onSelectt = (title) => {
    setQuery(title);
   }

   const onEnter = (e) => {
    if (e.key === 'Enter' && query.length >=3){
        history.push({
          pathname: '/shop',
          search: '?search=' + query
        })
    }
   }

   const onSearch = () => {
     if(query.length >=3){
        history.push({
          pathname: '/shop',
          search: '?search=' + query
        })
     }
   }

    return(
      <>
      {isMobile &&
      <Grid container item xs={3} justifyContent="flex-end"  alignItems="center" style={{paddingLeft:'5%'}}>
        <IconButton onClick={handleSearch}>
          <BiSearch />
        </IconButton>
      </Grid>
      }
      <Grid container item xs={12} sm={8} style={(!showSearchBox && isMobile) ? {position:'absolute',left:'-1000px'} : {}} className={(showSearchBox && isMobile) ? "search searchOnFly" : "search"}>
        <Grid 
        container 
        justifyContent={isMobile ? 'flex-end' : 'center'} 
        className="searchBox">
            <Autocomplete
              freeSolo
              id="search-input"
              // disableClearable
              fullWidth
              options= {searchInput}
              getOptionLabel={(option) => option.title || ''}
              renderOption={(props, option) => {
                  const { title, price} = option;
                  return (
                    <Grid container item xs={12} onClick={()=>onSelectt(title)}>
                      <p {...props} className='option' >
                        {title}
                      </p>
                    </Grid>
                  );
                }}
              renderInput={(params) => (
              <TextField
                  {...params}
                  placeholder={'جستجو'}
                  variant="outlined"
                  size="small"
                  onChange={setSearchQuery}
                  onKeyPress ={onEnter}
                  value={query}
                  InputProps={{
                    ...params.InputProps,
                    min :3,
                    max:10,
                    type: 'search',
                    endAdornment: (
                      <InputAdornment position="end">
                        <IconButton onClick={onSearch}>
                          <BiSearch />
                        </IconButton>
                      </InputAdornment>
                    )
                  }}
              />
              )}
            />
        </Grid>
      </Grid>
    </>
    );
}

export default translate(Search);