import React,{useState,useEffect} from 'react';
import {
    translate,
    t,
  } from 'react-switch-lang';

  import { Link } from "react-router-dom";

  import Grid from '@mui/material/Grid';
  import Typography from '@mui/material/Typography';

  import {useBreakpoints} from '../../../../hooks';

  import { Spin as Hamburger } from 'hamburger-react';
  import { useLocation } from 'react-router-dom';

function Nav(props){
   const {isMobile} = useBreakpoints();
   const [isOpen, setOpen] = useState(false);
   const location = useLocation();

   useEffect(()=>{
    setOpen(false);
   },[location])



    return(
        <>
        {
            isMobile && 
            <Grid container item xs={6} justifyContent="center" alignItems="flex-start" >
                <Hamburger size={28} color="black" toggled={isOpen} toggle={setOpen} />
            </Grid>
        }
        {
            isMobile &&
            <Grid item xs={12} className={isMobile ? ( isOpen ? "nav navMobile blury" : "nav navMobile closeNav") : "nav"}></Grid>
        }
        
        <Grid container item xs={12} className={isMobile ? ( isOpen ? "nav navMobile" : "nav navMobile closeNav") : "nav"}>
            <Grid item xs={2} className="menuItem">
                <Link to="/">
                    <Typography variant="body1" component="p">صفحه اصلی</Typography>
                </Link>
            </Grid>
            <Grid item xs={2} className="menuItem">
                <Link to="/shop">
                    <Typography variant="body1" component="p">محصولات</Typography>
                </Link>
            </Grid>
            <Grid item xs={2} className="menuItem">
                <Link to="/contact-us">
                    <Typography variant="body1" component="p">تماس با ما</Typography>
                </Link>
            </Grid>
            <Grid item xs={2} className="menuItem">
                <Link to="/about-us">
                    <Typography variant="body1" component="p">درباره ما</Typography>
                </Link>
            </Grid>
            <Grid item xs={2} className="menuItem">
                <Link to="/special-user">
                    <Typography variant="body1" component="p">مشتریان ویژه</Typography>
                </Link>
            </Grid>
            <Grid item xs={2} className="menuItem">
                <Link to="/multimedia">
                    <Typography variant="body1" component="p">رسانه</Typography>
                </Link>
            </Grid>
        </Grid>
        </>
    );
}

export default translate(Nav);