import React from 'react';
import { useHistory } from "react-router-dom";
import {Grid, Typography, Button } from '@mui/material';
import {persianNumber, persianMoneyFormat}  from '../../helpers';
import Spacer from '../spacer';
import DefaultDivider from '../divider';
import { AiOutlineClose } from "react-icons/ai";

function Cart(props){
    const {state:{purchaseItems},actions:{updatePurchasedCart,removeFromPurchaseCart}} = props;
    const history = useHistory();

    const realPrice = (price,discount) => price - ((price * discount) / 100);

    const realTotalPrice = () => purchaseItems.reduce((acc,item)=> acc + (realPrice(item.price,item.discount) * +item.purchasedQty),0);

    const totalPrice = () => purchaseItems.reduce((acc,item)=> acc + (item.price * +item.purchasedQty),0);

    const totalDiscount = () => purchaseItems.reduce((acc,item)=> acc + (((item.discount * item.price)/100) * item.purchasedQty),0);

    const totalDiscountPercent = () => purchaseItems.reduce((acc,item)=> acc + Math.round(+item.discount),0);

    const totalShipment = () => 0;

    const goToProduct = (id) => {history.push(`/shop/${id}`);}

    const increaseQuantity = (product) => {
        if(product.purchasedQty < product.quantity){
            const newProduct = {...product,purchasedQty: product.purchasedQty + 1};
            updatePurchasedCart(newProduct);
        }   
    }

    const decreaseQuantity = (product) => {
        const newProduct = {...product,purchasedQty: product.purchasedQty - 1};
        updatePurchasedCart(newProduct);
        if(product.purchasedQty <= 1){
            removeFromPurchaseCart(newProduct);
        }
    }

    return(
        <Grid container item xs={12} className="cartViewContainer" flexDirection="row-reverse">
            <Grid container item xs={12} className="header">
               <Typography variant="h6">سبد خرید <span>{`( ${purchaseItems.length && persianNumber(purchaseItems.length ? purchaseItems?.length : 0)}) کالا`}</span></Typography>
            </Grid>
            <DefaultDivider  />
            <Grid container item style={{position:"relative",minHeight:"320px"}} xs={  purchaseItems.length ? 8 : 12}>
            {
                purchaseItems.length ? purchaseItems.map((item,index)=>(
                    <Grid container item xs={12} className="cartItem">
                        <AiOutlineClose title="حذف محصول" className="remove" onClick={()=>removeFromPurchaseCart(item)} />
                        <Grid container item xs={4} onClick={()=>goToProduct(item.id)} className="cartImage">
                            <img src={item.img} alt={item.title}  />
                        </Grid>
                        <Grid container item xs={1} alignItems="center" justifyContent="center">
                            <DefaultDivider  style={{width:"1px",height:"90%", backgroundColor:"#ddd"}} />
                        </Grid>
                        <Grid container item xs={7}>
                            <Grid container item xs={12} className="title">
                                <Typography variant="h6" style={{cursor:"pointer"}} onClick={()=>goToProduct(item.id)}>{item.title}</Typography>
                            </Grid>
                            <Grid container item xs={12}>
                                <ul>
                                    <li style={{display:"flex",flexDirection:"column",justifyContent:"space-around"}}>
                                        <Typography variant="body1">{`قیمت واحد : ${persianMoneyFormat(item.price,true)}`}</Typography>
                                        <Typography variant="body1" style={{color:"red"}}>{`مبلغ تخفیف : ${persianMoneyFormat((item.price * item.discount) / 100,true)}`}</Typography>
                                    </li>
                                    <li>
                                        <Typography variant="h6" className="qtyButton" onClick={()=>decreaseQuantity(item)}>-</Typography>
                                        <Typography variant="body1">{persianNumber(item.purchasedQty)}</Typography>
                                        <Typography variant="h6" className="qtyButton" onClick={()=>increaseQuantity(item)}>+</Typography>
                                    </li>
                                    <li>
                                        <Typography variant="h6" style={{direction:"rtl"}}>{persianMoneyFormat(realPrice(item.price,item.discount) * item.purchasedQty,true)}</Typography>
                                    </li>
                                </ul>
                            </Grid>
                        </Grid>
                    </Grid>
                ))
                :
                    <Grid container item xs={12} justifyContent="flex-end">
                        <Spacer level={2} />
                        <Typography variant="body1">سبد خرید شما خالی می باشد</Typography>
                        <Spacer level={20} />
                    </Grid>
            }
            </Grid>
            {purchaseItems.length &&
            <Grid container item xs={4}  justifyContent="center" style={{height:"90%",position:"relative"}}>
                <Grid container item xs={11} className="totalPart">
                    <Grid container item xs={12} >
                        <Typography variant="h6">جزئیات خرید</Typography>
                    </Grid>
                    <Grid container item xs={12} justifyContent="space-between" >
                        <Typography variant="body1" style={{paddingRight:"15px"}}>مجموع خرید شما <span>{`( ${purchaseItems.length && persianNumber(purchaseItems.length ? purchaseItems?.length : 0)}) کالا`}</span></Typography>
                        <Typography variant="body1">{persianMoneyFormat(totalPrice(),true)}</Typography>
                        <DefaultDivider color ="#eee"  width={8} />
                    </Grid>
                    <Grid container item xs={12} justifyContent="space-between" >
                        <Typography variant="body1" style={{paddingRight:"15px"}}>سود شما از این خرید <span>{`${persianNumber(totalDiscountPercent()/purchaseItems.length)} %`}</span></Typography>
                        <Typography variant="body1">{persianMoneyFormat(totalDiscount(),true)}</Typography>
                        <DefaultDivider color ="#eee"  width={8} />
                    </Grid>
                    <Grid container item xs={12} justifyContent="space-between" >
                        <Typography variant="body1" style={{paddingRight:"15px"}}>هزینه ارسال</Typography>
                        <Typography variant="body1">{persianMoneyFormat(totalShipment(),true)}</Typography>
                        <DefaultDivider color ="#eee"  width={8} />
                    </Grid>
                    <Grid container item xs={12} justifyContent="space-between" >
                        <Typography variant="h6">مبلغ قابل پرداخت</Typography>
                        <Typography variant="h6">{persianMoneyFormat(realTotalPrice(),true)}</Typography>
                    </Grid>
                    <Grid container item xs={12}>
                        <Button className="button" variant="contained">
                            <Typography variant="body1"  >تکمیل فرایند خرید</Typography>
                        </Button>
                    </Grid>
                </Grid>
            </Grid>
            }
        </Grid>
    );
}

export default Cart;