/* eslint-disable default-case */
/**
 *
 * manipultaLocalStorage
 *
 */

import { USER_DATA_LS_NAME } from '../config/consts';

export function manipulateLocalStorage(type = 'LOAD-OBJECT', name, value) {
  switch (type) {
    case 'LOAD-OBJECT': {
      return (
        localStorage.getItem(name) && JSON.parse(localStorage.getItem(name))
      );
    }
    case 'SAVE-OBJECT': {
      return localStorage.setItem(name, JSON.stringify(value));
    }
    case 'SAVE-NUMBER': {
      return localStorage.setItem(name, String(value));
    }
    case 'LOAD-NUMBER': {
      return localStorage.getItem(name) && Number(localStorage.getItem(name));
    }
    default:
      return null;
  }
}

export const getLocalStorageToken = () => {
  const localUserData = manipulateLocalStorage(
    'LOAD-OBJECT',
    USER_DATA_LS_NAME,
  );
  return localUserData && localUserData.tokens;
};
