import React from 'react';
import {Grid, Typography} from '@mui/material';

import { useBreakpoints } from '../../hooks';

import { BiSortAlt2 } from "react-icons/bi";

import Spacer from '../spacer';


function Sort({defaultSort,setSort}){
    const {isMobile,isTablet, isDesktop, matches } = useBreakpoints('down','md');
    const [selected,setSelected] = React.useState(defaultSort);
    const [toggleSort, setToggleSort] = React.useState(false);
    const sortBy = (value) => {
        setSelected(value);
        setSort(value);
    }
    return(
        <Grid container item xs={matches ? 8 : 12} className="sort">
            {
                (isDesktop || toggleSort) &&
                <Grid container item xs={10} justifyContent="flex-end" 
                    style={(isTablet && toggleSort) ? 
                        { position:"absolute",top:"100px", height:"200px",width:"250px"} :
                         (isMobile && toggleSort) ? {position:"absolute",width:"100vw",top:"170px",left:"50px"} :  null}>
                    <ul className={`sortGroup ${selected}`}>
                        <li onClick={()=>sortBy('mostVisited')}>پربازدیدترین</li>
                        <li onClick={()=>sortBy('mostSelling')}>پرفروشترین</li>
                        <li onClick={()=>sortBy('mostFavorited')}>محبوبترین</li>
                        <li onClick={()=>sortBy('newest')}>جدیدترین</li>
                        <li onClick={()=>sortBy('cheapest')}>ارزانترین</li>
                        <li onClick={()=>sortBy('mostExpensive')}>گرانترین</li>
                    </ul>
                </Grid>
            }
            <Grid container item xs={12} md={2} className="sortIcon" justifyContent="center" alignItems="center" 
                flexDirection={matches ? "row-reverse" : "row"}
                onClick={matches ? ()=>setToggleSort(!toggleSort) : null}>
                <BiSortAlt2 />
                <Spacer level={2} space={0.3} />
                <Typography variant="body1">مرتب سازی</Typography>
            </Grid>
        </Grid>
    );
}

export default Sort;