import React, { memo } from 'react';
import { createSelector } from 'reselect';
import { connect } from 'react-redux';
import { compose } from 'redux';
import { deepmerge } from '../../helpers';

import {
  makeSelectPurchaseItems,
} from '../../stores/rootSelector';

import {
  addToPurchaseCart as addToPurchaseCartAction,
  updatePurchasedCart as updatePurchasedCartAction,
  removeFromPurchaseCart as removeFromPurchasedCartAction,
  clearPurchaseCart as clearPurchaseCartAction,
} from '../../stores/rootAction';

export function PurchaseProvider(props) {
  const { innerActions, innerState, children, ...rest } = props;
  const injectNewProps = {
    actions: innerActions,
    state: innerState,
    sideEffects: {
      // onClose: () => innerActions.clearPurchaseCart(),
    },
  };

  if (rest.actions)
    injectNewProps.actions = deepmerge(injectNewProps.actions, rest.actions);

  if (rest.state)
    injectNewProps.state = deepmerge(injectNewProps.state, rest.state);

  if (rest.sideEffects)
    injectNewProps.sideEffects = deepmerge(
      injectNewProps.sideEffects,
      rest.sideEffects,
    );

  return React.Children.map(children, child =>
    child ? React.cloneElement(child, injectNewProps) : null,
  );
}

const mapStateToProps = createSelector(
  makeSelectPurchaseItems(),
  (
    purchaseItems,
  ) => ({
    innerState: {
      purchaseItems,
    },
  }),
);

function mapDispatchToProps(dispatch) {
  return {
    innerActions: {
      addToPurchaseCart: props => dispatch(addToPurchaseCartAction(props)),
      updatePurchasedCart: props => dispatch(updatePurchasedCartAction(props)),
      removeFromPurchaseCart: props => dispatch(removeFromPurchasedCartAction(props)),
      clearPurchaseCart: () => dispatch(clearPurchaseCartAction()),
    },
  };
}

const withConnect = connect(
  mapStateToProps,
  mapDispatchToProps,
);

export default compose(
  withConnect,
  memo,
)(PurchaseProvider);
