export function addToPurchaseCart(item) {
  return {
    type: ADD_TO_PURCHASE_CART,
    payload: item,
  };
}

export function updatePurchasedCart(item){
  return {
    type: UPDATE_PURCHASE_CART,
    payload : item,
  }
}

export function removeFromPurchaseCart(item) {
  return {
    type: REMOVE_FROM_PURCHASE_CART,
    payload: item,
  };
}

export function clearPurchaseCart() {
  return {
    type: CLEAR_PURCHASE_CART,
  };
}

