import React from 'react';
import {Tabs, Tab, Grid, Typography, Box} from '@mui/material';
import { useTheme } from '@mui/material/styles';
import SwipeableViews from 'react-swipeable-views';

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}



function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

export default function ProductInfoView(props) {
  const theme = useTheme();
  const {product} = props;
  const {desc, properties} = product;
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const handleChangeIndex = (index) => {
    setValue(index);
  };

  return (
    <Grid container item xs={11} className="productInfoView">
      <Grid container item xs={12} style={{ borderBottom: 1, borderColor: 'divider' }}>
        <Tabs value={value} onChange={handleChange} aria-label="basic tabs example">
          <Tab label="درباره محصول" {...a11yProps(0)} wrapped={true} />
          <Tab label="خصوصیات محصول" {...a11yProps(1)} />
          <Tab disabled label="ارزش غذایی" {...a11yProps(2)} />
        </Tabs>
      </Grid>
      <SwipeableViews
        axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
        index={value}
        onChangeIndex={handleChangeIndex}
      >
      <TabPanel value={value} index={0} dir={'rtl'}>
        <Typography variant="body1">
          {desc}
        </Typography>
      </TabPanel>
      <TabPanel value={value} index={1} dir={'rtl'}>
        {
          properties?.map((prop, index) => (
            <Typography key={index}>{prop}</Typography>
          ))
        }
      </TabPanel>
      <TabPanel value={value} index={2} dir={theme.direction}>
        Item Three
      </TabPanel>
      </SwipeableViews>
    </Grid>
  );
}