import React from 'react';

import {Grid, Typography} from '@mui/material';

import {useBreakpoints} from '../../hooks';
 

function MainDescription(props){
    const {title, desc, img, reverse} = props;

  const {matches} = useBreakpoints('down','md');
  return (
    <Grid container  xs={12}  className="mainDescription">
      <div className="backgroundRect" />
        <Grid container item xs={12} className="image">
            <img src={img} alt={title} />
        </Grid>
        <Grid container  xs={12} className="description">
            <Typography style={{color:'#fff'}} variant="h6">{title}</Typography>
            <Typography variant="body2">{desc}</Typography>
        </Grid>
    </Grid>
  );
}

export default MainDescription;