import React from 'react';
import {
    translate,
    t,
  } from 'react-switch-lang';

  import {Grid, Typography ,Button, Modal , Backdrop, Box }from '@mui/material';
  import { AiOutlineUser } from "react-icons/ai";

import {useBreakpoints} from '../../../../hooks';
import { AiFillEyeInvisible } from "react-icons/ai";
import { MdOutlineVisibility } from "react-icons/md";
import Spacer from '../../../spacer';

import { useSpring, animated } from 'react-spring';

import Register from './register';

import { useHistory } from "react-router-dom";


function Account(props){
  const history = useHistory();
   const {isMobile, matches}= useBreakpoints('down','md');
   const [openAccount, setOpenAccount] = React.useState(false);
   const handleOpenRegister = () => history.push('/register');

   const [login, setLogin] = React.useState({
    user: '',
    password: '',
    showPassword: false,
    error: ''
  });

  const handleMouseIn = () => {
    setOpenAccount(true);
}

const handleMouseOut = () => {
    setOpenAccount(false);
}

  const handleChange = (prop) => (event) => {
    setLogin({ ...login, [prop]: event.target.value });
  };

  const handleClickShowPassword = () => {
    setLogin({
      ...login,
      showPassword: !login.showPassword,
    });
  };

  const handleMouseClick = () => {
    setOpenAccount(!openAccount);
  }

  // const handleMouseDownPassword = (event) => {
  //   event.preventDefault();
  // };

  const handleSubmit = () => {
    console.log(login);
  }

    return(
        <Grid 
            container
            item
            xs={3}
            sm={2}
            md={4} 
            justifyContent ="space-around" 
            alignItems="center" 
            className="account" onMouseOver={!matches ? handleMouseIn : null} onMouseLeave={!matches ? handleMouseOut : null}>
            <Grid container className={matches ? 'accountBox removeBorder' : 'accountBox'} >
               {
                  !matches && <Typography variant="body1" component="p">حساب کاربری</Typography>
               } 
                <AiOutlineUser className="accountIcon" onClick={handleMouseClick} />
            </Grid>
            <Grid container item xs={12} className={`accountModal ${openAccount ? 'showAccount' : 'hideAccount'}`} onMouseOver={!matches ? handleMouseIn : null}>
               <form>
                  <Grid container item xs={12} className="label"><Typography variant="body1">نام کاربری</Typography></Grid>
                  <Grid container item xs={12} className="inputBox"><input type="text" onChange={(e)=>handleChange('user')(e)} /></Grid>
                  <Grid container item xs={12} className="label"><Typography variant="body1">کلمه عبور</Typography></Grid>
                  <Grid container item xs={12} className="inputBox">
                    <input type={login.showPassword ? "text" : "password"} onChange={(e)=>handleChange('pass')(e)} />
                    {
                      login.showPassword ? 
                      <AiFillEyeInvisible className="showPass" onClick={handleClickShowPassword} />
                      :
                      <MdOutlineVisibility className="showPass" onClick={handleClickShowPassword} />
                    }
                  </Grid>
                  {login.error &&
                  <Grid container item xs={12} className="loginError">
                    <Typography variant="caption">نام کاربری و یا کلمه عبور اشتباه است.</Typography>
                  </Grid>
                  }
                  <Spacer level={5} />
                  <Grid container item xs={12} justifyContent="space-around">
                    <Grid container item xs={5}>
                      <Button className="button" variant="contained" onClick={handleSubmit}>
                        <Typography variant="body1">ورود</Typography>
                      </Button>
                    </Grid>
                    <Grid container item xs={5}>
                      <Button className="button" variant="contained" onClick={handleOpenRegister}>
                        <Typography variant="body1">ثبت نام</Typography>
                      </Button>
                    </Grid>
                  </Grid>
                  <Grid container item xs={12}><Typography className="forgetPass" variant="body2">فراموشی کلمه عبور</Typography></Grid>
               </form>
            </Grid>
        </Grid>
    );
}

export default translate(Account);