import React from 'react';

import {Grid , Typography} from '@mui/material';
import { AiOutlineDown } from "react-icons/ai";

import Category from '../category';

function ProductsCategorization(props){
    const { categorization, filters, setFilter } = props;
    const [rotate,setRotate] = React.useState(false);
    const onCloseFilter = () => {
        setRotate(!rotate);
    }
    return(
        <Grid container item xs={12} className="productCategorization">
            <Grid container item xs={12} className="header" style={{borderBottom :rotate ? "1px solid azure" : "1px solid black"}}>
                <Grid container item xs={10} alignItems="center">
                    <Typography  variant='body1'>دسته بندی محصولات</Typography>
                </Grid>
                <Grid container item xs={2} justifyContent="flex-end" alignItems="center">
                    <AiOutlineDown className={rotate ? 'rotateDown' : 'rotateUp'} onClick={onCloseFilter} />
                </Grid>
            </Grid>
            <Grid container item xs={12} className="body" style={{maxHeight: rotate ? 0 : "1000px",padding : rotate ? "0 10px" : "10px"}}>
               {
                   categorization.map((category,index)=> <Category key={index} category={category} filters={filters} setFilter={setFilter} />)
               }
            </Grid>
        </Grid>
    );
}

export default ProductsCategorization;