import React from "react";
import {
    EmailShareButton,
    FacebookShareButton,
    // HatenaShareButton,
    // InstapaperShareButton,
    // LineShareButton,
    // LinkedinShareButton,
    // LivejournalShareButton,
    // MailruShareButton,
    // OKShareButton,
    // PinterestShareButton,
    // PocketShareButton,
    // RedditShareButton,
    TelegramShareButton,
    // TumblrShareButton,
    TwitterShareButton,
    // ViberShareButton,
    // VKShareButton,
    WhatsappShareButton,
    // WorkplaceShareButton,
    FacebookIcon,
    WhatsappIcon,
    TelegramIcon,
    TwitterIcon,
    EmailIcon
  } from "react-share";

  import Spacer from "../spacer";
  
  import {Grid} from '@mui/material';

  function SocialShare(props){
      const {title,url,size} = props;
      return(
        <Grid container item xs={10} justifyContent="space-around" className="shareSocial" spacing={2}>
            <WhatsappShareButton url={url} title={title}>
                <WhatsappIcon round size={size} />
            </WhatsappShareButton>
            <TelegramShareButton url={url} title={title}>
                <TelegramIcon round size={size}  />
            </TelegramShareButton>
            <FacebookShareButton url={url} quote={title}>
                <FacebookIcon round size={size}  />
            </FacebookShareButton>
            <TwitterShareButton url={url} title={title}>
                <TwitterIcon round size={size}  />
            </TwitterShareButton>
        </Grid>
      );
  }

  export default SocialShare;