import React from 'react';
import {
    Link, 
  } from "react-router-dom";
import {Grid, Typography} from '@mui/material';

import Spacer from '../spacer';


function GroupAddress(props){
    const { addresses, seprator = '>' , md=12} = props;
    return(
        <Grid container item xs={12} className="groupAddress">
            <Grid container item xs={12} md={md} >
            {
                addresses.map((address,index)=>(
                    <React.Fragment key={index}>
                        <Spacer level={2} space={0.2} />
                        {index !== addresses.length - 1 ?
                        <Link to={address.url}>
                            <Typography variant="body1">{address.name}</Typography>
                        </Link>
                        :
                        <Typography variant="body1">{address.name}</Typography>
                        }
                        <Spacer level={2} space={0.2} />
                    {index !== addresses.length - 1 && <Typography variant="body1">{` ${seprator} `}</Typography>}
                        <Spacer  level={2} space={0.2} />
                    </React.Fragment>
                ))
            }
                
            </Grid>
        </Grid>
    );
}

export default GroupAddress;