import React from "react";

import {Grid} from '@mui/material';

function LoadPage(){
    return(
        <Grid container item xs={12} className="loadPage">
            <div className="container">
                <div className="row">
                    <div id="loader">
                        <div className="dot"></div>
                        <div className="dot"></div>
                        <div className="dot"></div>
                        <div className="dot"></div>
                        <div className="dot"></div>
                        <div className="dot"></div>
                        <div className="dot"></div>
                        <div className="dot"></div>
                        <div className="lading"></div>
                    </div>
                </div>
            </div>
        </Grid>
    );
}

export default LoadPage;