import React,{useState,useEffect} from "react";
// import {useBreakpoints} from '../hooks';
import _ from 'lodash';


import ProductCart from '../productCart';
import Spacer from '../spacer';

import {Grid,Typography, Pagination, PaginationItem} from '@mui/material';
import {useBreakpoints} from '../../hooks';

function ProductList(props){
    const {products, filters, sort, search} = props;
    const [finalProducts,setFinalProducts] = useState([]);
    const {matches} = useBreakpoints('down','md');
// console.log(filters);

const checkSeason = () => {
    let today = new Date();
    let mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!

    switch(+mm){
        case 0,1,2: 
            return 'Winter';
        case 3,4,5: 
            return 'Spring';
        case 6,7,8: 
            return 'Summer';
        case 9,10,11: 
            return 'Autmn';
        default:
            return 'Spring';
    }

}

useEffect(()=>{
    const checkStock = filters.filter(item=>item.name === 'موجود در انبار')[0]?.value;
    const price = filters.filter(item => item.name === 'قیمت')[0]?.value;
    const season = filters.filter(item => item.name === 'محصولات فصلی')[0]?.value;
    const futureCheck = filters.filter(item => item.name === 'موجود تا چند روز آینده')[0]?.value;
    const shipable = filters.filter(item => item.name === 'محصولات غیر قابل ارسال')[0]?.value;
    const groupArray = filters.filter(item => item.name.includes('دسته بندی'));

    const seasonIndex = (season) => {
        return season[0] && checkSeason() === 'Spring' ? 0 :
        season[1] && checkSeason() === 'Summer' ? 1 :
        season[2] && checkSeason() === 'Autmn' ? 2 :
        season[3] && checkSeason() === 'Winter' ? 3 : 4;
    }

    const finalProduct = products.filter(item => 
        (search ? _.includes(item.title,search) : item) &&
        (checkStock ? item.quantity > 0 : item) &&
        (price?.length > 0 ? item.price > price[0] && item.price < price[1] : item) &&
        (season ? item.season[seasonIndex(item.season)] : item) &&
        (futureCheck ? item.futureQuantity > 0 : item) && 
        (shipable ? item.shipable : item) && (
            (groupArray?.length > 0 ? item.group?.includes(groupArray[0]?.value.slice(3)) : item) ||
            (groupArray?.length > 0 ? item.group?.includes(groupArray[1]?.value.slice(3)) : item) ||
            (groupArray?.length > 0 ? item.group?.includes(groupArray[2]?.value.slice(3)) : item) ||
            (groupArray?.length > 0 ? item.subGroup?.includes(groupArray[0]?.value.slice(3)) : item) ||
            (groupArray?.length > 0 ? item.subGroup?.includes(groupArray[1]?.value.slice(3)) : item) ||
            (groupArray?.length > 0 ? item.subGroup?.includes(groupArray[2]?.value.slice(3)) : item) 
        )
    );

    finalProduct.sort((a,b)=>{
        if(sort === 'mostVisited'){
           return +b.visit - +a.visit;
        }
        if(sort === 'mostSelling'){
            return +b.sell - +a.sell;
        }
        if(sort === 'mostFavorited'){
            let firstRate = Number(a.rate),secondRate = Number(b.rate);
            return secondRate - firstRate;
        }
        if(sort === 'newest'){
            let da = new Date(a.createDate),
                db = new Date(b.createDate);
                return da - db;
        }
        if(sort === 'cheapest'){
            return +a.price - +b.price;
        }
        if(sort === 'mostExpensive'){
            return +b.price - +a.price;
        }
        return 0;
    });

    setFinalProducts(finalProduct);
},[filters, products, sort,search]);
   

    return(
        <Grid container item xs={12} className="productList">

            <Grid container item xs={12} spacing={matches ? 1 : 2}  
                justifyContent={matches ? "center" : "flex-start"} 
                alignItems="flex-start"
                style={{marginLeft:"15px",minHeight:"100%"}}>
            {
                finalProducts.length ? finalProducts.map((product,index)=>(
                    <Grid item key={index}>
                        <ProductCart product={product} width='200px' rate hover  />
                    </Grid>
                ))
                :
                <Grid container item xs={12} justifyContent="center">
                    <Spacer level={5} />
                    <Typography variant="h5">محصولی قابل نمایش نمی باشد</Typography>
                    <Spacer level={15} />
                </Grid>
            }
            <Spacer level={3} />
{            
            // <Grid container item xs={12} justifyContent="center">
            //     <Pagination
            //         count={10}
            //     />
            // </Grid>
            }
            <Spacer level={3} />
            </Grid>
        </Grid>
    );
}

export default ProductList;