import React from 'react';
import { useHistory } from "react-router-dom";
import {persianMoneyFormat, persianDiscount} from '../../helpers';
import Spacer from '../spacer';

import '@splidejs/splide/dist/css/themes/splide-default.min.css';

import {Grid, Typography, Rating} from '@mui/material';
import { transform } from 'lodash';


function ProductCart(props) {
    const {product, className='', width ='200px', shadow = false, discountSlider = false, border= false, hover=false} = props;
    const { id,img, title, shortDesc, price, discount, rate } = product;

    const currentPrice = price - ((price * discount) / 100);
    const history = useHistory();

    const handleProduct = (id) =>{
        history.push(`/shop/${id}`);
    }

    return(
        <Grid container 
            style={{maxWidth: width,boxShadow:shadow ? '0 1px 2px #000' : 'none', border:border ? '1px solid #999' : 'none'}}
            className={`productCart ${className} ${hover && 'animation'}`}
            onClick={()=>handleProduct(id)}
         >
            {
                img && 
                <Grid container style={{maxHeight: discountSlider ? '110px' : width}} className="productImg">
                    <img src={img} alt={title} />
                </Grid>
            }
            <Grid container className="productInfo">
                <Grid container className="productTitle">
                    <Typography variant="body1">
                        {title}
                    </Typography>
                </Grid>
                <Grid container className="productDesc">
                    <Typography variant="caption">
                        {shortDesc}
                    </Typography>
                </Grid>
                {+discount ?
                <Grid container className="productDiscount">
                    <Typography className="oldPrice" variant="body2">
                        {persianMoneyFormat(price,true)}
                    </Typography>
                    <Typography className='discount' variant="body2">
                        {persianDiscount(discount)}
                    </Typography>
                </Grid>
                : <Spacer level={4} />
                }
                <Grid container className="productPrice">
                    <Typography variant="body1">
                    {persianMoneyFormat(currentPrice,true)}
                    </Typography>
                </Grid>
                {
                    rate && 
                    <Grid container justifyContent="center" style={{direction: 'ltr'}} >
                        <Rating name="half-rating-read" value={rate} precision={0.5} readOnly  />
                    </Grid>
                }
            </Grid>
        </Grid>
    );
}

export default ProductCart;