import React from 'react';

import Spacer from '../../../spacer';

import { Grid, Typography } from '@mui/material';

import { AiOutlineLeft } from "react-icons/ai";


function Category(props){
    const { category, setFilter, filters } = props;
    const [rotateMain,setRotateMain] = React.useState(false);
    const [rotateTree,setRotateTree] = React.useState([false,false]);

    const onRotateMain = () => {
        setRotateMain(!rotateMain);
        setRotateTree([false,false])
    }
    const onRotateTree = (index) =>{ 
        const tree = [...rotateTree];
        tree[index] = !tree[index];
        setRotateTree(tree);
    }

    const applyCategory = (name) => {
        const newFilter = filters.filter(item => item.name !== `دسته بندی(${name})`);
        setFilter(oldFilter => ([...newFilter,{name: `دسته بندی(${name})`, value: name}]));
    }

    return(
        <Grid container item xs={12} className="category">
            <Grid container item xs={12} className="main">
                <div className="title bolder">
                    <Typography variant="p">{category.main}</Typography>
                </div>
                <div className={`icon ${rotateMain ? 'rotateDown' : 'rotateUp'}`} onClick={onRotateMain}>
                    <AiOutlineLeft />
                </div>
            </Grid>
            {category.tree && category.tree.map((item,index)=>(
                <>
                <Grid container item xs={11} className="main" style={{transition:"height 0.7s",height:rotateMain ? "30px" : 0}}>
                    <div className="title bold hideText" style={{height:rotateMain ? "30px" : 0}} >
                        <Typography onClick={()=>applyCategory(item.name)} variant="p" style={{cursor:"pointer"}}>{item.name}</Typography>
                    </div>
                    <div className={`icon ${rotateTree[index] ? 'rotateDown' : 'rotateUp'}`}  style={{height:rotateMain ? "30px" : 0}} onClick={()=>onRotateTree(index)}>
                        <AiOutlineLeft />
                    </div>
                </Grid>
                {
                    item.subTree &&  item.subTree.map(name=>(
                        <Grid container item xs={10} className="main" >
                            <div className="title link hideText" style={{height:rotateTree[index] ? '30px' : 0}}>
                                <Typography onClick={()=>applyCategory(name)} variant="p">{name}</Typography>
                            </div>
                        </Grid>
                    ))
                }
                </>
                ))
            
            }
        </Grid>
    );
}

export default Category;