import React,{useEffect} from "react";
import Spacer from '../components/spacer';
import {useBreakpoints, useLoading, useScrollToTop} from '../hooks';
import {  useParams } from "react-router-dom";

import LoadPage from '../components/loadPage';
import GroupAddress from '../components/groupAddress';
import ProductCartView from '../components/productCartView';
import ProductInfoView from '../components/productInfoView';
import Slider from '../components/slider';
import {bestSellProducts} from '../mock/bestSellProducts';

import {Grid} from '@mui/material';

import {products} from '../mock/products';

export function ProductView(){

    const {loading} = useLoading(3);
    let { id } = useParams();
    useScrollToTop(id);
    const {matches, isDesktop, isMobile} = useBreakpoints('down','md');

    const addresses = [
        {
            name:'صفحه اصلی',
            url:'/'
        },
        {
            name:'فروشگاه',
            url:'/shop'
        }
    ];

    const productTitle = products[id-1].title;
    addresses.push({
        name: productTitle,
        url: `/shop/${productTitle}`
    });



    return(
        <div  style={{position:"relative", maxWidth:"100%"}}>
        {
            loading ? <LoadPage />
            : (
                <>
                <Spacer level={isMobile ? 5 : 2} />
                <Grid container item xs={12}>
                <Spacer level={2} />
                    <Grid container item xs={12} justifyContent={matches ? "flex-start" : "center"}>
                        <GroupAddress addresses={addresses} md={6} />
                    </Grid>
                    <Spacer level={2} />
                    <Grid container item xs={12} justifyContent="center">
                        <ProductCartView product={products[id-1]}  />
                    </Grid>
                    <Spacer level={2} />
                    <Grid container item xs={12} justifyContent="center">
                        <ProductInfoView product={products[id-1]}  />
                    </Grid>
                    <Spacer level={3} />
                    <Grid container justifyContent="center">
                        <Grid container item xs={11}  justifyContent="center" >
                            <Slider slides = {bestSellProducts} title="محصولات مشابه" perPage={4} loop />  
                        </Grid>
                    </Grid>
                <Spacer level={2} />
                </Grid>
                </>
            )
        }
        </div>
    );
}