import React from 'react';
import {Grid, Typography} from '@mui/material';

import { GoSettings } from "react-icons/go";

import Spacer from "../spacer";


function Filter({setOpenFilter, openFilter}){

    return(
        <Grid container item xs={12} className="filter">
            <Grid container item xs={8} className="filterIcon" onClick={()=>setOpenFilter(!openFilter)}>
                <GoSettings />
                <Spacer space={0.3} level={2} />
                <Typography variant="body1">فیلتر</Typography>
            </Grid>
        </Grid>
    );
}

export default Filter;