import React from 'react';

import Spacer from "../../../spacer";

import { Grid, Typography, Slider, Button } from '@mui/material';
import {persianMoneyFormat} from '../../../../helpers';
import { BiFilterAlt } from "react-icons/bi";




function Price(props){
  const {filters, setFilter} = props;
    const minDistance = 50000;
    const [value1, setValue1] = React.useState(filters.filter(item=>item.name === "قیمت")[0]?.value ? filters.filter(item=>item.name === "قیمت")[0].value : [0, 1000000]);

  const handleChange1 = (event, newValue, activeThumb) => {

    if (!Array.isArray(newValue)) {
      return;
    }

    if (activeThumb === 0) {
      setValue1([Math.min(newValue[0], value1[1] - minDistance), value1[1]]);
    } else {
      setValue1([value1[0], Math.max(newValue[1], value1[0] + minDistance)]);
    }
  };

  function valueLabelFormat(value) {
    return `${persianMoneyFormat(value,true)}`;
  }

  const applyPrice = () => {
   const oldValue = filters.filter(item=>item.name === "قیمت")[0]?.value;
   if(!oldValue){
    setFilter(oldFilter => ([...oldFilter,{name:"قیمت",value:value1}]))
   }
   else{
    const newFilter = filters.filter(item=>item.name !== "قیمت");
    setFilter(oldFilter => ([...newFilter,{name:"قیمت",value:value1}]))
   }
    
  }

    return(
        <Grid container item xs={12} className="price">
            <Grid container item xs={12} className="header">
                <Typography  variant='body1'>محدوده قیمت موردنظر</Typography>
            </Grid>
            <Grid container item xs={12} className="body">
                <Grid container item xs={9}>
                  <Slider
                    getAriaLabel={() => 'Minimum distance shift'}
                    min={0}
                    max={1000000}
                    step={10000}
                    value={value1}
                    onChange={handleChange1}
                    valueLabelDisplay="auto"
                    valueLabelFormat={valueLabelFormat}
                    disableSwap
                  />
                </Grid>
                <Spacer level={2} />
                <Grid container item xs={12}>
                  <Grid container item xs={6} justifyContent="center">
                    <Typography variant='p' style={{padding:"0 5px"}}> تا </Typography>
                    <Typography variant="p" style={{padding:"0 5px"}}> {persianMoneyFormat(value1[1],true)} </Typography>
                  </Grid>
                  <Grid container item xs={6} justifyContent="center">
                    <Typography variant='p' style={{padding:"0 5px"}}> از </Typography>
                    <Typography variant="p" style={{padding:"0 5px"}}> {persianMoneyFormat(value1[0],true)} </Typography>
                  </Grid>
                </Grid>
                <Spacer level={2} />
                <Grid container item xs={12} justifyContent="center">
                  <Button variant="contained" endIcon={<BiFilterAlt />} onClick={applyPrice}>
                    <Typography variant='p' style={{padding:"0 5px"}}> اعمال محدوده قیمت</Typography>
                  </Button>
                </Grid>
            </Grid>
        </Grid>
    );
}

export default Price;