import React from 'react';

import {useBreakpoints} from '../../hooks';

import FilteredItems from './components/filteredItems';
import ProductsCategorization from './components/productsCategorization';
import RadioBtn from './components/radioBtn';
import Price from './components/price';
import SpecialOffer from './components/specialOffer';
import Spacer from "../spacer";

import { Grid } from '@mui/material';
import { AiOutlineClose } from "react-icons/ai";

function FilterGroup(props){
    const { filters, setFilters, categorization, setOpenFilter} = props;
    const {matches} = useBreakpoints('down','md');

    return(
        <Grid container item xs={12} className="filterGroup">
            <Grid container style={{direction:"ltr"}}>
                {matches && <Grid container item xs={12} className="closeFilter"><AiOutlineClose onClick={()=>{setOpenFilter(false)}} /></Grid>}
                <Spacer level={1} />
                <FilteredItems filters={filters} setFilters={setFilters} />
                <Spacer level={1} />
                <ProductsCategorization categorization={categorization} filters={filters} setFilter={setFilters} />
                <Spacer level={1} />
                <RadioBtn title="موجود در انبار" filters={filters} setFilter={setFilters} />
                <Spacer level={1} />
                <RadioBtn title="موجود تا چند روز آینده" filters={filters} setFilter={setFilters} />
                <Spacer level={1} />
                <RadioBtn title="محصولات فصلی" filters={filters} setFilter={setFilters} />
                <Spacer level={1} />
                <Price filters={filters} setFilter={setFilters} />
                <Spacer level={1} />
                <RadioBtn filters={filters} title="محصولات غیر قابل ارسال" setFilter={setFilters} />
                <Spacer level={1} />
                <SpecialOffer />
                <Spacer level={1} />
            </Grid>
        </Grid>
    );
}

export default FilterGroup;