// import './styles/index.scss';
import './app.scss';
import {
  setDefaultLanguage,
  setTranslations
} from 'react-switch-lang';

import { Provider } from 'react-redux';
import { ConnectedRouter } from 'connected-react-router';
import history from './utils/history';
import configureStore from './stores/rootStore';

import { BrowserRouter,Switch, Route } from "react-router-dom";
import {CssBaseline} from '@mui/material';
import { withStyles } from '@mui/styles';

import globalStyles from './utils/globalStyle';

import en from './language/en.json';
import fa from './language/fa.json';

import Header from './components/header';
import Footer from './components/footer';
import UnderConstruction from './components/underConstruction';
import NotFound from './components/notFound';

import {Home, Shop, ProductView, Cart, RegisterPage} from './pages';

function App() {
  setDefaultLanguage('en');
  setTranslations({en, fa});

  const initialState = {};
  const store = configureStore(initialState, history);

  return (
    <Provider store={store}>
      <ConnectedRouter history={history}>
        <BrowserRouter>
        <CssBaseline />
          <Header />
          <Switch>
            <Route path="/multimedia" render={()=><UnderConstruction />} />
            <Route path="/special-user" render={()=><UnderConstruction />} />
            <Route path="/about-us" render={()=><UnderConstruction />} />
            <Route path="/contact-us" render={()=><UnderConstruction />} />
            <Route path="/privacy-policy" render={()=><UnderConstruction />} />
            <Route path="/complaints" render={()=><UnderConstruction />} />
            <Route path="/rules" render={()=><UnderConstruction />} />
            <Route path="/contact-us" render={()=><UnderConstruction />} />
            <Route path="/shop/:id" render={()=><ProductView />}/>
            <Route path="/shop" render={()=><Shop />}/>
            <Route path="/cart" render={()=><Cart />}/>
            <Route path="/register" render={()=><RegisterPage />}/>
            <Route path="/" render={()=><Home/>} exact />
            <Route path="/" render={()=><NotFound />} />
          </Switch>
          <Footer />
        </BrowserRouter>
      </ConnectedRouter>
    </Provider>
  );
}

export default withStyles(globalStyles)(App);
