import React,{useState} from 'react';
import { IoIosCart } from "react-icons/io";
import {Grid, Typography, Divider, Button  } from '@mui/material';
import {useBreakpoints}  from '../../../../hooks';
import {persianNumber, persianMoneyFormat}  from '../../../../helpers';
import Spacer from '../../../spacer';
import { useHistory } from "react-router-dom";
import { AiOutlineClose } from "react-icons/ai";


function CartMain(props){
    const {state:{purchaseItems}, actions:{removeFromPurchaseCart}} = props;
    const {isMobile, matches} = useBreakpoints('down','md');
    const [openCart,setOpenCart] = useState(false);
    const history = useHistory();

    const realPrice = (price,discount) => {
        return price - ((price * discount) / 100);
    }

    const total = () => {
        return purchaseItems.reduce((acc,item)=> acc + (realPrice(item.price,item.discount) * +item.purchasedQty),0);
    }

    const handleMouseIn = () => {
        setOpenCart(true);
    }

    const handleMouseOut = () => {
        setOpenCart(false);
    }

    const handleMouseClick = () => {
        setOpenCart(!openCart);
    }

    const goToProduct = (id) => {
        history.push(`/shop/${id}`);
    }

    const goToCart = (id) => {
        history.push('/cart');
    }

    const removeItem = (item) => {
        removeFromPurchaseCart(item);
    }

    return(
        <Grid 
            container
            item
            xs={3}
            sm={2}
            md={4}
            justifyContent ="space-around"
            alignItems="center" 
            className="cart" onMouseOver={!isMobile ? handleMouseIn : null} onMouseLeave={!isMobile ? handleMouseOut : null}>
            <Grid 
                container 
                className={!matches ? 'cartBox' : 'cartBox removeBorder'}
                justifyContent ={isMobile ? "flex-start" : "space-around" } >
                {
                !matches && 
                    <Typography variant="body1" component="p">سبد خرید</Typography>
                }
                <Typography variant="body2" className="quantity"
                 style={purchaseItems && purchaseItems.length ? {backgroundColor:"rgb(255, 216, 216)"} : {backgroundColor:"white"}}>
                    {purchaseItems && purchaseItems.length ? persianNumber(purchaseItems.length) : ""}
                </Typography>
                <IoIosCart className="icon" onClick={handleMouseClick} />
            </Grid>
            <Grid container className={`cartView ${openCart ? 'showCart' : 'hideCart'}`} onMouseOver={!isMobile ? handleMouseIn : null} >
               {isMobile && <div className="closeCart"><AiOutlineClose className="remove" onClick={handleMouseOut} /></div>}
                <Grid container item xs={12} style={{padding:"10px"}} justifyContent="center" alignItems="center">
                {purchaseItems && purchaseItems.length ?
                <>
                    <Grid container item xs={12} >
                        <ul style={{direction:"rtl"}}>
                            <li></li>
                            <li><Typography variant="body2" style={{color:"rgb(255, 223, 223)"}}>شماره</Typography></li>
                            <li><Typography variant="body2" style={{color:"rgb(255, 223, 223)"}}>نام محصول</Typography></li>
                            <li><Typography variant="body2" style={{color:"rgb(255, 223, 223)"}}>تعداد</Typography></li>
                            <li><Typography variant="body2" style={{color:"rgb(255, 223, 223)"}}>قیمت واحد</Typography></li>
                            <li><Typography variant="body2" style={{color:"rgb(255, 223, 223)"}}>قیمت نهایی</Typography></li>
                        </ul>
                    </Grid>
                    <Divider style={{width:"100%",height:"2px",backgroundColor:"rgb(255, 255, 255)"}} />
                    {purchaseItems.map((item,index)=>(
                    <Grid container item xs={12} justifyContent="center">
                        <ul>
                            <li onClick={()=>removeItem(item)}><AiOutlineClose className="remove" /></li>
                            <li><Typography variant="body2">{persianNumber(index+1)}</Typography></li>
                            <li onClick={()=>goToProduct(item.id)} style={{cursor:"pointer"}}><Typography variant="body2">{item.title}</Typography></li>
                            <li><Typography variant="body2">{persianNumber(item.purchasedQty)}</Typography></li>
                            <li><Typography variant="body2">{persianMoneyFormat(realPrice(item.price,item.discount))}</Typography></li>
                            <li><Typography variant="body2">{persianMoneyFormat(item.purchasedQty * realPrice(item.price,item.discount))}</Typography></li>
                        </ul>
                        <Divider style={{width:"90%",height:"1px",backgroundColor:"#bbb"}} />
                    </Grid>
                    ))}
                    <Grid container item xs={12}>
                        <ul>
                            <li style={{minWidth:"70%"}}><Typography variant="body2">مبلغ قابل پرداخت</Typography></li>
                            <li style={{minWidth:"30%"}}><Typography variant="body2">{persianMoneyFormat(+total(),true)}</Typography></li>
                        </ul>
                    </Grid>
                    <Spacer level={1} />
                    <Grid container item xs={12}>
                        <Button className="button" variant="contained" onClick={goToCart}>
                            <Typography variant="body1"  >تکمیل فرایند خرید</Typography>
                        </Button>
                    </Grid>
                </>
                :( 
                <>
                    <Typography variant="body1">سبد خرید خالی می باشد</Typography>
                    <Spacer level={2} />
                    <Divider style={{width:"90%",height:"1px",backgroundColor:"#777"}} />
                    <Spacer level={1} />
                    <Typography variant="body2">از منو محصولات خرید خود را تکمیل کنید</Typography>
                </>)
                }
                </Grid>
            </Grid>
        
        </Grid>
    );
}

export default CartMain;