import React from 'react';
import { Splide, SplideSlide } from '@splidejs/react-splide';
import '@splidejs/splide/dist/css/themes/splide-default.min.css';
import Spacer from "../../../spacer";

import { Grid, Typography } from '@mui/material';

function SpecialOffer(props){
    const { filters} = props;

    return(
        <Grid container item xs={12} className="specialOffer">
            <Grid container item xs={12} className="header">
                <Typography  variant='body1'>تخفیف فصلی</Typography>
            </Grid>
            <Spacer level={1} />
            <Grid container item xs={12}>
                <Splide
                    className="slider"
                    options={ {
                    rewind: true,
                    autoplay     : true,
                    perMove: 1,
                    gap   : '1rem',
                    speed : 300,
                    arrows       : 'slider',
                    pagination : false,
                    height: '200px',
                    perPage    : 1,
                    }}
                >

                {
                [1,2,3].map((slide,index)=>(
                    <SplideSlide key={index}>
                        <Grid container item xs={12} justifyContent="center" alignItems="center" className="offers">
                            <Typography variant="h5">{index}</Typography>
                        </Grid>
                    </SplideSlide>
                ))
                }
                </Splide>
            </Grid>
        </Grid>
    );
}

export default SpecialOffer;