import React from 'react';
import { useSpring, animated } from "react-spring";
import {Grid, Typography} from '@mui/material';

import {useHistory} from 'react-router-dom';

function Group(props){
    const {img, title, xs, width, height, ...rest} = props;
    const config = {
        mass: 5,
        tension: 249,
        friction: 5,
        precision: 0.01,
        velocity: 0.01
      };

      const history = useHistory();

    const [styles, api]= useSpring(()=>({
        config,
        to: [
          { opacity: 1,
            transform: 'translate3d(0,0,0) scale(1) rotateX(0deg)'},
        ],
        from: { opacity: 1,
            transform: 'translate3d(0,0,0) scale(1) rotateX(0deg)'},
    }));

    const handleClick = () => {
        api.start({transform: 'translate3d(0,0,0) scale(0.92) rotateX(0deg)'});
        let [firstTitle,secondTitle] = title.split(' و ');
        let query = `?groupFirstTitle=${firstTitle}` + (secondTitle ? `&groupSecondTitle=${secondTitle}` : '');

        history.push({
            pathname: '/shop',
            search: query
        })
      }

    return(
    <Grid container  style={{height,maxWidth:width,}} >
    <animated.div className="groupContainer" style={styles}>
        <Grid container   {...rest}  className="group" style={{backgroundImage:`url(${img})`,
        backgroundPosition: 'bottom left',
        backgroundSize: 'auto 70%',
        backgroundRepeat: 'no-repeat'}}
            onMouseEnter={()=>api.start({transform: 'translate3d(0,0,0) scale(0.99) rotateX(-10deg)'})}
            onMouseLeave={()=>api.start({transform: 'translate3d(0,0,0) scale(1) rotateX(0deg)'})}
            onClick={handleClick}>
            
            <Typography variant="h6" className="title">
                {title}
            </Typography>
            
        </Grid>
        </animated.div>
        </Grid>
    );
}

export default Group;