import { createSelector } from 'reselect';
import { initialState } from './reducer';

/**
 * Direct selector to the purchase state domain
 */

const selectPurchaseDomain = state => state.purchase || initialState;

/**
 * Other specific selectors
 */
const makeSelectPurchaseItems = () =>
  createSelector(
    selectPurchaseDomain,
    substate => substate.items,
  );

