import React from 'react';
import clsx from 'clsx';

import { useBreakpoints } from '../../hooks';

function Spacer(props) {
  const { level, unit=10, theme, space=10 , style } = props;
  const { matches } = useBreakpoints('down', 'sm');

  const finalLevel = level || (matches ? 5 : 8);

  return (
    <div
      className={clsx('Spacer', `${theme}`)}
      style={{ height: `${finalLevel * unit}px`,width:`${space * unit}%`, ...style }}
    />
  );
}

export default Spacer;
