import React from 'react';

import Tag from '../tag';

import { Grid, Typography } from '@mui/material';
import { AiOutlineDown } from "react-icons/ai";

import { useHistory } from 'react-router-dom'

function FilteredItems(props){
    const { filters , setFilters } = props;
    const [rotate,setRotate] = React.useState(false);
    const onCloseFilter = () => {
        setRotate(!rotate);
    }

    const history = useHistory();

    const removeFilter = (title) => {
        const result = filters.filter(({name}) => !name.includes(title));
        setFilters(result);
        if(title.includes('جستجو')){
            history.replace({
                search: '',
              })
        }
    }

    const removeAll = () => {
        const search = filters.filter(item=>item.name.includes('جستجو'));

        if(search.length > 0){
            history.replace({
                search: '',
              })
        }
        setFilters([]);
    }

    return(
        <Grid container item xs={12} className="filteredItems">
            <Grid container item xs={12} className="header" style={{borderBottom :rotate ? "1px solid azure" : "1px solid black"}}>
                <Grid container item xs={(filters !== undefined && filters.length !== 0) ? 7 :10}>
                    <Typography  variant='body1'>فیلتر های اعمال شده</Typography>
                </Grid>
                {(filters !== undefined && filters.length !== 0) &&
                <Grid container item xs={3} className="remove" onClick={removeAll}>
                    <Typography variant='body1'>حذف همه</Typography>
                </Grid>
                }
                <Grid container item xs={2} justifyContent="flex-end" alignItems="center">
                    <AiOutlineDown className={rotate ? 'rotateDown' : 'rotateUp'} onClick={onCloseFilter} />
                </Grid>
            </Grid>
            <Grid container item xs={12} className="body" style={{maxHeight: rotate ? 0 : "1000px",padding : rotate ? "0 10px" : "10px"}}>
            {
                filters.map((filter,index) => <Tag key={index} title={filter.name} removeFilter={removeFilter} />)
            }
            </Grid>
        </Grid>
    );
}

export default FilteredItems;