// higher order function that takes options then a price and will return the formatted price
import persianJs from 'persianjs';

export function moneyFormat(
  value,
  {
    sign = '$',
    delimiter = ',',
    decimal = '.',
    append = false,
    precision = 2,
    round = true,
    custom,
  } = {},
) {
  const e = [1, 10, 100, 1000, 10000, 100000, 1000000, 10000000];

  // eslint-disable-next-line no-param-reassign
  value = round
    ? Math.round(value * e[precision]) / e[precision]
    : parseFloat(value);

  const pieces = value
    .toFixed(precision)
    .replace('.', decimal)
    .split('');

  let ii = pieces.length - (precision ? precision + 1 : 0);

  // eslint-disable-next-line no-cond-assign
  while ((ii -= 3) > 0) {
    pieces.splice(ii, 0, delimiter);
  }

  if (typeof custom === 'function') {
    return custom({
      sign,
      float: value,
      value: pieces.join(''),
    });
  }

  return append ? pieces.join('') + sign : sign + pieces.join('');
};

export function persianMoneyFormat(value,haveSign){
  return persianJs(moneyFormat(value,{precision:0,append:true,sign: haveSign ? ' تومان ' : ''})).englishNumber().toString();
};

export function persianDiscount(value){
  return persianJs(moneyFormat(value,{precision:0,append:false,sign: '%'})).englishNumber().toString();
};

export function persianNumber(value){
  return persianJs(value).englishNumber().toString();
}
