import React,{useState,useEffect} from 'react';
import {persianMoneyFormat, persianDiscount} from '../../helpers';
import { useHistory, useLocation } from "react-router-dom";
import {Grid, Typography, Rating, Divider, Chip, Button, Modal, Checkbox, FormControlLabel} from '@mui/material';

import Spacer from "../spacer";
import { BsFillShareFill } from "react-icons/bs";
import { MdNotificationsActive } from "react-icons/md";
import { AiOutlineClose } from "react-icons/ai";

import SocialShare from '../socialShare';


function ProductCartMain(props) {
    const {product, user, actions, state} = props;
    
    const history = useHistory();
    const location = useLocation();

    if(!product){
        history.push('/');
        history.go(0);
    }

    const { id=0,img='', title='', desc='', price='', discount='', group='', quantity='', shortDesc='' } = product;
    const {purchaseItems} = state;
    const selectedItem = purchaseItems &&  purchaseItems.filter(item => item.id === id);
    const {addToPurchaseCart, updatePurchasedCart, removeFromPurchaseCart} = actions;

    const [purchasedQty,setPurchasedQty] = useState(0);
    useEffect(()=>{
        if(selectedItem && selectedItem.length > 0){
            setPurchasedQty(selectedItem[0].purchasedQty);
        }
        else{
            setPurchasedQty(0);
        }
        
    },[purchasedQty,selectedItem])

    const { email = "someone@email.com", mobile = "09011515382" } = user ?? {};
    const currentPrice = price - ((price * discount) / 100);

    const [openShare, setOpenShare] = React.useState(false);
    const handleOpenShare = () => setOpenShare(true);
    const handleCloseShare = () => setOpenShare(false);

    const [openNotify, setOpenNotify] = React.useState(false);
    const handleOpenNotify = () => setOpenNotify(true);
    const handleCloseNotify = () => setOpenNotify(false);

    const [notifyEmailCheck, setNotifyEmailCheck] = React.useState(false);
    const [notifyMobileCheck, setNotifyMobileCheck] = React.useState(false);

    const url = location.pathname;

    const handleChangeEmailNotify = (event) => {
        setNotifyEmailCheck(!notifyEmailCheck);
    }
    const handleChangeMobileNotify = (event) => {
        setNotifyMobileCheck(!notifyMobileCheck);
    }

    const addToCart = () => {
        setPurchasedQty(qty =>(  qty + 1));

        const newProduct = {...product,purchasedQty: purchasedQty + 1};
        addToPurchaseCart(newProduct);
    }

    const increaseQuantity = () => {
        if(purchasedQty < quantity){
            setPurchasedQty(qty =>(  qty + 1));
            const newProduct = {...product,purchasedQty: purchasedQty + 1};
            updatePurchasedCart(newProduct);
        }   
        
    }

    const decreaseQuantity = () => {
        setPurchasedQty(qty =>(  qty - 1));
        const newProduct = {...product,purchasedQty: purchasedQty - 1};
        updatePurchasedCart(newProduct);
        if(purchasedQty <= 1){
            removeFromPurchaseCart(newProduct);
        }
    }

    const ShareModal = () => (
        <Modal
        open={openShare}
        onClose={handleCloseShare}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Grid container  className="shareModal" style={{width:"200px"}}>
          <AiOutlineClose className="closeIcon" onClick={handleCloseShare} />
          <Typography id="modal-modal-title" variant="h6" component="h2">اشتراک گذاری</Typography>
          <Divider  style={{width:"100%"}} />
          <Typography id="modal-modal-title" variant="body2" component="span">با استفاده از روش های زیر میتوانید این صفحه را با دوستان خود به اشتراک بگذارید.</Typography>
          <Spacer level={1} />
          <SocialShare title={title} url={url} size={22} />
        </Grid>
      </Modal>
    );

    const NotifyModal = () => (
        <Modal
        open={openNotify}
        onClose={handleCloseNotify}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Grid container  className="shareModal" style={{width:"300px"}}>
        <AiOutlineClose className="closeIcon" onClick={handleCloseNotify} />
            <Grid container item xs={12}>
                <Typography id="modal-modal-title" variant="body1">موجود شد به من خبر بده</Typography>
            </Grid>
            <Grid container item xs={5} justifyContent="center">
                <img src="../images/other/notify.png" alt="notify" width="100px" height="100px" />
            </Grid>
            <Grid container item xs={7} className="notifyCheckContainer">
                <Typography id="modal-modal-title" variant="caption">از طریق : </Typography>
                <Divider  style={{width:"100%"}} />
                <Spacer level={1} />
                <FormControlLabel
                    style={{fontSize:"12px"}}
                    label= {`ایمیل به ${email}`}
                    control={<Checkbox
                        checked={notifyEmailCheck}
                        onChange={handleChangeEmailNotify}
                        inputProps={{ 'aria-label': 'controlled' }}
                    />}
                />
                <FormControlLabel
                    label= {`پیامک به  ${mobile}`}
                    control={<Checkbox
                        checked={notifyMobileCheck}
                        onChange={handleChangeMobileNotify}
                        inputProps={{ 'aria-label': 'controlled' }}
                    />}
                />
            </Grid>
        </Grid>
      </Modal>
    );

    return(
        <Grid container item xs={11} className="productCartView">
            <Grid container item xs={6} className="cartSideDesign">
                <Grid container item xs={3} className="logo" >
                    <Typography variant="h3">YEGANEH</Typography>
                </Grid>
                <Grid container className="shareIcon">
                    <BsFillShareFill onClick={handleOpenShare} />
                </Grid>
                <ShareModal />
                <NotifyModal />
                <Grid container className="notifyIcon">
                    <MdNotificationsActive onClick={handleOpenNotify} />
                </Grid>
            </Grid>
            <Grid container item xs={11} className="cartContainer">
                <Grid container item xs={12} justifyContent="center"  className="rateContainer" >
                    <Rating name="half-rating-read" defaultValue={4} precision={0.5} readOnly size="large" className="rate" />
                </Grid>
                <Grid container item xs={3} className="status">
                    <Typography variant="body2">جدید</Typography>
                </Grid>
                
            {
                img && 
                <Grid container item xs={12} sm={6} className="productImage">
                    <img src={`../${img}`} alt={title} />
                </Grid>
            }
                <Grid container item xs={12} sm={6} className="productInfo">
                    <Grid container item xs={12} style={{paddingRight:"20px"}}>
                        <Typography variant="h4">{title}</Typography>
                    </Grid>
                    <Spacer level={1} />
                    <Divider  style={{width:"100%"}} />
                    <Grid container item xs={12}>
                        <Typography variant="body1">{quantity ? "موجود در انبار" : "در انبار موجود نیست"}</Typography>
                    </Grid>
                    <Grid container item xs={12}>
                        <Typography variant="body1">دسته بندی : <span style={{color:"red"}}>{group}</span></Typography>
                    </Grid>
                    {
                        shortDesc && 
                        <Grid container item xs={12}>
                            <Typography variant="body1">{shortDesc}</Typography>
                        </Grid>
                    }
                    <Divider  style={{width:"100%"}} />
                    <Grid container item xs={12}>
                        <Typography variant="body1">قیمت مصرف کننده : </Typography>
                    </Grid>
                    <Grid container item xs={12} style={{direction:"ltr",maxHeight:"40px"}}>
                        <Typography variant="body1"><Chip style={{backgroundColor:"red",color:"white",fontSize:"16px"}} label={persianDiscount(discount)} /></Typography>
                        <Spacer space={1} />
                        <Typography variant="body1" className="oldPrice">{persianMoneyFormat(price,true)}</Typography>
                    </Grid>
                    <Grid container item xs={12} style={{direction:"ltr",maxHeight:"50px"}}>
                        <Typography variant="h5" style={{direction:"rtl"}}>{persianMoneyFormat(currentPrice,true)}</Typography>
                    </Grid>
                    <Spacer level={3} />
                    <Grid container item xs={9} justifyContent="center">
                        {!purchasedQty ? (
                        <Button className="button" variant="contained" disabled = {quantity ? false : true} onClick={addToCart}>
                            <Typography variant="body1" style={{color:'white'}} >افزودن به سبد خرید</Typography>
                        </Button>
                        ) :
                        (
                        <Button className="button" style={{cursor:"default"}} variant="contained" disabled = {quantity ? false : true}>
                            <Typography variant="h4" className="miniBtn" style={{left:'25px'}} onClick={decreaseQuantity} >-</Typography>
                            <Typography variant="body1" style={{color:'white'}} >{persianMoneyFormat(purchasedQty,false)}</Typography>
                            <Typography variant="h4" className="miniBtn" style={{right:'25px'}} onClick={increaseQuantity} >+</Typography>
                        </Button>
                        )
                        }
                    </Grid>
                </Grid>
            </Grid>
            
        </Grid>
    );
}

export default ProductCartMain;