import React from 'react';

import {Divider} from '@mui/material';

function DefaultDivider(props){
    const { width= 10, level=1, color, text , textAlign ,vertical, style , ...rest } = props;
    return(
        <Divider 
            className="divider"
            textAlign={textAlign}
            orientation={`${vertical ? 'vertical' : 'horizontal'}`}
            flexItem = {vertical ? true : false}
            style={{width:`${width * 10}%` ,margin: `0 ${(10 - width)/0.2}%` ,height: `${level}px`,backgroundColor: color , ...style}}
            {...rest}
        >
                {text ? text : ''}
        </Divider>
    );
}

export default DefaultDivider;