import React from 'react';
import { Splide, SplideSlide } from '@splidejs/react-splide';

import '@splidejs/splide/dist/css/themes/splide-default.min.css';

import {Grid} from '@mui/material';

import ProductCart from '../productCart';

import {useBreakpoints} from '../../hooks';


function DiscountSlider(props) {
  const {slides} = props;
  const {matches} = useBreakpoints('down','lg');
  return(
    <Grid container className="discountSliderContainer">
      <Splide
        className="discountSlider"
        options={ {
        rewind: true,
        gap   : '0.5rem',
        speed : 500,
        arrows       : 'slider',
        pagination : false,
        direction : matches ? 'ltr' : 'ttb',
        height: matches ? '220px' : '470px',
        perPage    : 2,
        // arrowPath: '>',
        breakpoints: {  
          1300:{
            perPage    : 2,
          },
          1170:{
            perPage    : 3,
          },
          640: {
            perPage: 1,
          },
          450: {
            perPage: 1,
          },

        },
        } }>

        {
          slides.map((slide,index)=>(
            <SplideSlide  style={{display:"flex",margin:'none',padding:0,minWidth: matches ? '25%' : '100%',justifyContent:'center',alignItems:'center'}}>
                <ProductCart product={slide} discountSlider shadow />
            </SplideSlide>
          ))
        }
      </Splide>
    </Grid>
  );
}

export default DiscountSlider;