import React from 'react';

import {Grid , Typography, Switch} from '@mui/material';


function RadioBtn(props){
    const { title, filters, setFilter } = props;
    const [checked, setChecked] = React.useState(false);

    React.useEffect(()=>{
        let oldCheck = filters.filter(item=>item.name === title)[0]?.value;
        setChecked(oldCheck);
    // eslint-disable-next-line react-hooks/exhaustive-deps
    },[filters]);

    const handleChange = (event) => {
      setChecked(event.target.checked);
      if(event.target.checked){
        setFilter(filters => ([...filters,{name:title,value:event.target.value}]))
      }
      else{
          const newFilters = filters.filter(item=> item.name !== title);
        setFilter(filters => ([...newFilters]))
          
      }     
      
    };

    return(
        <Grid container item xs={12} className="radioBtn">
            <Grid container item xs={9} className="header">
                <Typography variant="body1" >{title}</Typography>
            </Grid>
            <Grid container item xs={3} className="body">
                <Switch
                checked={checked ?? false}
                onChange={handleChange}
                color="primary"
                inputProps={{ 'aria-label': 'uncontrolled' }}
                />
            </Grid>
        </Grid>
    );
}

export default RadioBtn;