import React from "react";
import SlideShow from '../components/slideShow';
import DiscountSlider from '../components/discountSlider';
import Slider from '../components/slider';
import Season from '../components/season';
import ProductsGroup from '../components/productsGroup';
import MainDescription from '../components/mainDescription';
import Spacer from '../components/spacer';
import {bestSellProducts} from '../mock/bestSellProducts';

import "./index.scss";

import {useBreakpoints, useLoading} from '../hooks';

import {Grid} from '@mui/material';

import Group from '../components/groupCard';
import LoadPage from '../components/loadPage';

export function Home(){

    const {isDesktop ,matches} = useBreakpoints('down','md');

    const slides = [
        {
            url:'images/slide-show/slide1.jpg',
            title:'محصولات خانگی یگانه',
            desc:'الویت ما رضایت شماست'
        },
        {
            url:'images/slide-show/slide2.jpg',
            title:'حلوای بنتو',
            desc:'سوغاتی مخصوص'
        },
        {
            url:'images/slide-show/slide3.jpg',
            title:'ترشی خانگی',
            desc:'همیشه همراه شما'
        },
        {
            url:'images/slide-show/slide2.jpg',
            title:'مربای زنجبیل',
            desc:'الویت ما رضایت شماست'
        }
    ];

    const discountSlides = [
        {
            id:1,
            img:'images/discount/jam.jpg',
            title:'محصولات خانگی یگانه',
            desc:'الویت ما رضایت شماست',
            price:'10000',
            discount:'10'
        },
        {
            id:2,
            img:'images/discount/jam.jpg',
            title:'حلوای بنتو',
            desc:'سوغاتی مخصوص',
            price:'50000',
            discount:'10'
        },
        {
            id:3,
            img:'images/discount/jam.jpg',
            title:'ترشی خانگی',
            desc:'همیشه همراه شما',
            price:'20000',
            discount:'20'
        },
        {
            id:4,
            img:'images/discount/jam.jpg',
            title:'مربای زنجبیل',
            desc:'الویت ما رضایت شماست',
            price:'100000',
            discount:'5'
        }
    ];


    const season = {
        title: 'بهاری دلچسب را با محصولات خانگی یگانه تجربه کنید',
        img: 'images/season/spring.jpg'
    }

const {loading} = useLoading(3);

    return(
    <div  style={{position:"relative",maxWidth:"100%",padding:"0 15px",boxSizing:"border-box",backgroundColor:"rgb(255,250,255)"}}>
    {
        loading ? <LoadPage />
        : (
            <>
    <Spacer level={0.5} />
        <Grid container item xs={20} columns={20}  justifyContent="space-around" direction={isDesktop ? 'row-reverse' : 'column'}>
            <Grid container justifyContent="center" xs={20} lg={15}>
                <SlideShow slides={slides} />
            </Grid>
            <Grid container  justifyContent="center" xs={20} md={18} lg={4}>
                <DiscountSlider slides={discountSlides}  />
            </Grid>
        </Grid>
        <Grid container item xs={12} justifyContent="center" style={{marginTop:'10px'}} >
            <Grid container item xs={12} >
                <Grid container item xs={12} sm={4} justifyContent="center">
                    <MainDescription title="محصولات خانگی یگانه" desc="لورم ایپسوم متن ساختگی با تولید سادگی نامفهوم از صنعت چاپ، و با استفاده از طراحان گرافیک استا،."  img="images/main-page/image1-mainPage.jpg"  />
                </Grid>
                <Grid container item xs={12} sm={4} justifyContent="center">
                    <MainDescription title="محصولات خانگی یگانه" desc="لورم ایپسوم متن ساختگی با تولید سادگی نامفهوم از صنعت چاپ، و با استفاده از طراحان گرافیک استا،."  img="images/main-page/image1-mainPage.jpg"  />
                </Grid>
                <Grid container item xs={12} sm={4} justifyContent="center">
                    <MainDescription title="محصولات خانگی یگانه" desc="لورم ایپسوم متن ساختگی با تولید سادگی نامفهوم از صنعت چاپ، و با استفاده از طراحان گرافیک استا،."  img="images/main-page/image1-mainPage.jpg"  />
                </Grid>
            </Grid>
        </Grid>
        <Spacer level={2}/>
        <Grid container  justifyContent="center" >
            <Slider slides = {bestSellProducts} title=" محصولات پر بازدید روزانه" perPage={4} loop />  
        </Grid>
        <Spacer level={1} />
        <Grid container justifyContent="center">
            <Season title={season.title} img={season.img} />
        </Grid>
        <Grid container  justifyContent="center" >
            <ProductsGroup />
        </Grid>
        <Grid container  justifyContent="center" >
            <Slider slides = {bestSellProducts} title="محصولات پر فروش" perPage={4} loop />  
        </Grid>
        <Grid container justifyContent="center">
            <Grid container item xs={12} justifyContent="center">
                <Group title="سوغاتی جات"  width="33.3%" height={matches ? "120px" : "250px"} img='images/groups/soghati.jpg' />
                <Group title="مکمل و غذاهای دارویی" width="33.3%" height={matches ? "120px" : "250px"} img='images/groups/organic.jpg' />
                <Group title="سبزیجات"  width="33.3%" height={matches ? "120px" : "250px"} img='images/groups/vegetables.png' />
            </Grid>
        </Grid>
        <Spacer level={2} />
        </>
        )}
    </div>
    );

}