import React from 'react';
import {Grid, Typography, Button} from '@mui/material';
import { SocialIcon } from 'react-social-icons';
import {ReactComponent as Quality} from '../../assets/icons/bartar.svg';
import {ReactComponent as Leaf1} from '../../assets/icons/barg1.svg';
import {ReactComponent as Leaf2} from '../../assets/icons/barg2.svg';
import {ReactComponent as Health} from '../../assets/icons/health.svg';

import {useBreakpoints} from '../../hooks';

import { Link } from 'react-router-dom';

import Spacer from '../spacer';

function Footer(){
    const {isDesktop ,matches} = useBreakpoints('down','md');

    return(
        <Grid container  className="footer">
            <Grid container item xs={12} md={4} className="left">
                <Grid container item xs={6} md={12} className="leftBox" style={{paddingLeft: matches ? 0 : '150px'}}>
                    <Leaf1 />
                    <Typography variant="caption">همیشه تازه</Typography>
                   <Typography variant={matches ? "caption" : "body1"} textAlign="center">کلیه محصولات تازه و محصول همان فصل هستند</Typography>
                </Grid>
                <Grid container item xs={6} md={8} className="leftBox">
                    <Health />
                    <Typography variant="caption">دوست دار سلامتی</Typography>
                    <Typography variant={matches ? "caption" : "body1"} textAlign="center">محصولات یگانه حامی سلامتی شماست</Typography>
                </Grid>
            </Grid>
            <Grid container item xs={12} md={4} className="center" justifyContent="center">
                <Grid container item xs={10} justifyContent="center" className="centerBox">
                    <Typography variant={matches ? "body1" : "h5"}>چرا ما را انتخاب کنید</Typography>
                    <Typography variant={matches ? "body1" : "h6"} textAlign="center">در فروشگاه یگانه کلیه محصولات ارگانیک هستند و با بالاترین کیفیت ارائه می شوند</Typography>
                    <Grid container justifyContent="center" >
                        <SocialIcon url="https://twitter.com/yeganeh" className="socialIcon" />
                        <SocialIcon url="https://facebook.com/yeganeh" className="socialIcon" />
                        <SocialIcon url="https://instagram.com/yeganeh" className="socialIcon" />
                        <SocialIcon url="https://telegram.com/yeganeh" className="socialIcon" />
                        <SocialIcon url="https://whatsapp.com/yeganeh" className="socialIcon" />
                    </Grid>
                </Grid>
                {!matches && <Grid container item xs={12} md={12} justifyContent="center" className="centerBox">
                    <img src='images/main-page/footer.png' alt="footer" className="image" />
                </Grid>}
            </Grid>
            <Grid container item xs={12} md={4} justifyContent="flex-end" className="right">
                <Grid container item xs={6} md={12} className="rightBox" style={{paddingRight:matches ? 0 : '150px'}}>
                    <Leaf2 />
                    <Typography variant="caption">طبیعی</Typography>
                    <Typography variant={matches ? "caption" : "body1"} textAlign="center">محصولات یگانه ضامن سلامتی شماست</Typography>
                </Grid>
                <Grid container item xs={6} md={12} className="rightBox" style={{paddingRight:matches ? 0 : '20px'}}>
                    <Quality />
                    <Typography variant="caption">کیفیت برتر</Typography>
                    <Typography variant={matches ? "caption" : "body1"} textAlign="center">کلیه محصولات دارای کیفیت برتر می باشند</Typography>
                </Grid>
            </Grid>
            <Grid container item xs={12} justifyContent="center" className="bottom">
            <Spacer level={1} />
                <Grid container item xs={12} justifyContent="center" alignItems="center">
                    <Grid container item xs={12} justifyContent="center" alignItems="center">
                        <Typography variant="body2" >جهت عضویت در خبرنامه ایمیل خود را وارد نمایید</Typography>
                        <Spacer level={0.5} />
                    </Grid>
                    <Grid container item xs={10} md={6} justifyContent="center" flexDirection="row-reverse" className="subscriber">
                        <input type="email" placeholder='person@email.com' max="20" />
                        <Button className="button" variant="contained">عضویت</Button>
                        <Spacer level={0.5} />
                    </Grid>
                </Grid>
                <Grid container item xs={12} sm={6} md={4} justifyContent="center" className="footerText">
                    <Grid container item xs={12} justifyContent="center" alignItems="center">
                        <img src='./images/other/enamad.jpg' alt="enamad" className="enamad" />
                    </Grid>
                    <Grid container item xs={12}  justifyContent="center" alignItems="center"  flexDirection="column">
                        <Typography variant="h5">ساعات کاری</Typography>
                        <Spacer level={2} />
                        <Typography variant="body1">شنبه تا چهارشنبه از 7 صبح الی 7 شب</Typography>
                        <Spacer level={2} />
                        <Typography variant="body1">پنجشنبه از 7 صبح الی 12 ظهر</Typography>
                        <Spacer level={3} />
                    </Grid>
                </Grid>
                <Grid container item xs={12} sm={6} md={4}  justifyContent="center" alignItems="flex-end" flexDirection="column" className="footerText">
                    <Typography variant="h5">ارتباط با ما</Typography>
                    <Spacer level={3} />
                    <Typography variant="body1" style={{textAlign:"right"}}><span>آدرس :</span> استان مرکزی , محلات , خیابان شقایق پلاک 123</Typography>
                    <Spacer level={2} />
                    <Typography variant="body1"><span>تلفن :</span> 03454678967</Typography>
                    <Spacer level={2} />
                    <Typography variant="body1" dir="rtl"><span>ایمیل : </span> yeganeh.mahalat@gmail.com</Typography>
                    <Spacer level={3} />
                </Grid>
                <Grid container item xs={12} sm={12} md={4} justifyContent="center" alignItems="flex-end"  flexDirection="column" className="footerText">
                    <Typography variant="h5">خدمات مشتریان</Typography>
                    <Spacer level={3} />
                    <Typography variant="body1"><Link to={'/rules'}>قوانین</Link></Typography>
                    <Spacer level={2} />
                    <Typography variant="body1"><Link to={'/privacy-policy'}>حریم شخصی</Link></Typography>
                    <Spacer level={2} />
                    <Typography variant="body1"><Link to={'/complaints'}>انتقادات و شکایات</Link></Typography>
                    <Spacer level={3} />
                </Grid>
            </Grid>
        </Grid>
    );
}

export default Footer;