import React from 'react';
import {Grid, Typography ,Button, Modal , Backdrop, Box }from '@mui/material';
import { AiOutlineUser } from "react-icons/ai";

import {useBreakpoints} from '../../../../hooks';
import { AiFillEyeInvisible } from "react-icons/ai";
import { MdOutlineVisibility } from "react-icons/md";
import Spacer from '../../../spacer';

import { useSpring, animated } from 'react-spring';

function Register(props){
// const { handleCloseRegister, handleOpenRegister, openRegister } = props;


const strongRegex = new RegExp("^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#\$%\^&\*])(?=.{8,})");
const mediumRegex = new RegExp("^(((?=.*[a-z])(?=.*[A-Z]))|((?=.*[a-z])(?=.*[0-9]))|((?=.*[A-Z])(?=.*[0-9])))(?=.{6,})");

const [passInfoColor,setPassInfoColor] = React.useState('red');



    const [login, setLogin] = React.useState({
        user: '',
        password: '',
        rePassword:'',
        showPassword: false,
        error: false
      });

    const handleClickShowPassword = () => {
        setLogin({
          ...login,
          showPassword: !login.showPassword,
        });
      };

      const handleChange = (prop) => (event) => {
        if(prop === 'password'){
          setPassInfoColor(strongRegex.test(event.target.value) ? 'rgb(195, 255, 205)' : mediumRegex.test(event.target.value) ? 'yellow' : 'rgb(255, 170, 170)')
        }
       
        setLogin({ ...login, [prop]: event.target.value });
      };

      const onRegister = () => {
        if(login.user === '' && login.password === '' ){
          setLogin({...login, error: 'نام کاربری و رمز عبور نمی تواند خالی باشد'});
        }
        else if(login.password !== login.rePassword){
          setLogin({...login, error: 'رمز عبور و تکرار رمز عبور برابر نمی باشند'});
        }
        else{
          setLogin({...login, error: ''});
          console.log('register complete',login);
        }
      }


    return (
      <Grid container item xs={12} className="registerContainer">
      <Grid container item xs={12} className= 'blurEffect' style={{ 
        backgroundImage: `url(${process.env.PUBLIC_URL + '/images/other/register2.jpg'})` 
      }}  ></Grid>
          <Grid container item xs={12} justifyContent='center' className='register'>
              <Grid container item xs={6} className='accountModal'>
                <Grid container item xs={12} className="header">
                  <Typography variant="h5">ایجاد حساب کاربری</Typography>
                </Grid>
                <Spacer level={1} />
                <form>
                  <Grid container item xs={12} className="label"><Typography variant="body1">شماره همراه</Typography><Spacer level={1} /></Grid>
                  <Grid container item xs={12} className="inputBox">
                    <input type="text" onChange={(e)=>handleChange('user')(e)} />
                  </Grid>
                  <Grid container item xs={12} className="label"><Typography variant="body1">کلمه عبور</Typography><Spacer level={1} /></Grid>
                  <Grid container item xs={12} style={{justifyContent:"flex-end"}} className="inputBox">
                  {login.password && <Typography variant="body2" style={{position:'absolute',top:-15,left:0,zIndex:10, color:passInfoColor,paddingLeft:"40px"}}>{passInfoColor === 'rgb(255, 170, 170)' ? 'ضعیف' : passInfoColor === 'yellow' ? 'متوسط' : 'قوی' }</Typography>}
                    <input type={login.showPassword ? "text" : "password"} onChange={(e)=>handleChange('password')(e)} />
                    {
                      login.showPassword ? 
                      <AiFillEyeInvisible className="showPass"  onClick={handleClickShowPassword} />
                      :
                      <MdOutlineVisibility className="showPass"  onClick={handleClickShowPassword} />
                    }
                  </Grid>
                  <Grid container item xs={12} className="label"><Typography variant="body1">تکرار کلمه عبور</Typography><Spacer level={1} /></Grid>
                  <Grid container item xs={12} className="inputBox">
                    <input type={login.showPassword ? "text" : "password"} onChange={(e)=>handleChange('rePassword')(e)} />
                    {
                      login.showPassword ? 
                      <AiFillEyeInvisible className="showPass"  onClick={handleClickShowPassword} />
                      :
                      <MdOutlineVisibility className="showPass"  onClick={handleClickShowPassword} />
                    }
                  </Grid>
                  {login.error &&
                  <Grid container item xs={12} className="loginError">
                    <Typography variant="caption">{login.error}</Typography>
                  </Grid>
                  }
                  <Spacer level={1} />
                  <Grid container item xs={12} justifyContent="center">
                    <Grid container item xs={10}>
                      <Button className="button" variant="contained" onClick={onRegister}>
                        <Typography variant="body1">ثبت نام</Typography>
                      </Button>
                    </Grid>
                    <Spacer level={1} />
                  </Grid>
                </form>
              </Grid>
          </Grid>
      </Grid>
    );
}

export default Register;