import React, { useState, useEffect } from 'react';

import _ from 'lodash';

import {Grid, Typography} from '@mui/material';

import Group from '../groupCard';

import {useBreakpoints} from '../../hooks';
 

function ProductsGroup(){

  const {matches} = useBreakpoints('down','md');

  return (
    <Grid container  xs={11} className="productsGroup">
      <div className="firstPart">
        <Group title="ترشیجات" xs={12} width="99%" height={matches ? "150px" : "500px"} img='images/groups/pickle.jpg' />
        {matches && <Group title="ادویه جات" xs={12} width="99%" height="150px" img='images/groups/spices.png' />}
      </div>
      <div className="secondPart">
        {!matches && <Group title="ادویه جات" xs={12} width={matches ? "98%" : "99%"} height={matches ? "150px" : "250px"} img='images/groups/spices.png' />}
        <Group title="شربت ها و عرقیجات" xs={6}  width={matches ? "98%" : "48.5%"}  height={matches ? "150px" : "250px"} img='images/groups/syroup.png' />
        <Group title="شیره ها و مرباجات" xs={6}  width={matches ? "98%" : "48.5%"}  height={matches ? "150px" : "250px"} img='images/groups/jam.jpg' />
      </div>
    </Grid>
  );
}

export default ProductsGroup;