/*
 *
 * Purchase reducer
 *
 */
import produce from 'immer';

// import { reqIdlePayload } from 'utils/apiHandler';
import { manipulateLocalStorage } from '../../helpers';

import {
  ADD_TO_PURCHASE_CART,
  UPDATE_PURCHASE_CART,
  REMOVE_FROM_PURCHASE_CART,
  CLEAR_PURCHASE_CART,
  // CHECKOUT_PURCHASE,
  // CLEAR_SUBMIT_PURCHASE,
} from './constants';

export const initialState = {
  items: manipulateLocalStorage('LOAD-OBJECT', 'CART', 0)?.items || [],
  // checkout: reqIdlePayload,
};

/* eslint-disable default-case, no-param-reassign */
const purchaseReducer = (state = initialState, action) =>
  produce(state, draft => {
    const { type, payload } = action;
    switch (type) {
      case ADD_TO_PURCHASE_CART: {
        draft.items.push(payload);
        manipulateLocalStorage('SAVE-OBJECT', 'CART', draft);
        break;
      }
      case UPDATE_PURCHASE_CART: {
        const itemIndex = draft.items.findIndex(v => v.id === payload.id);
        draft.items[itemIndex] = payload;
        manipulateLocalStorage('SAVE-OBJECT', 'CART', draft);
        break;
      }
      case REMOVE_FROM_PURCHASE_CART: {
        const itemIndex = draft.items.findIndex(v => v.id === payload.id);
        draft.items.splice(itemIndex, 1);
        manipulateLocalStorage('SAVE-OBJECT', 'CART', draft);
        break;
      }
      case CLEAR_PURCHASE_CART:
        draft.items = [];
        break;
      // case CLEAR_SUBMIT_PURCHASE:
      //   draft.checkout = reqIdlePayload;
      //   draft.validPurchaseMethods = reqIdlePayload;
      //   break;
    }
    return draft;
  });

export default purchaseReducer;
